<template>
<div>
  <div class="title">
    <div class="mainTitle">{{title.text}}</div>
    <div class="subTitle">{{title.subText}}</div>
  </div>
  <chart class="chart" :options="option" ref="chart" style="width: 800px;min-height:200px" />
</div>
</template>

<script>
  import ECharts from 'vue-echarts/components/ECharts'
  import echartsGl from 'echarts-gl'
  import CommonWrapper from '@/components/commonWrapper'

  import 'echarts/lib/chart/line'
  import 'echarts/lib/component/polar'
  import 'echarts/lib/component/tooltip'
  import 'echarts/lib/component/legend'
  import 'echarts/lib/component/title'
const color = ['#313695', '#4575b4', '#74add1', '#abd9e9', '#e0f3f8', '#ffffbf', '#fee090', '#fdae61', '#f46d43', '#d73027', '#a50026'] ;
  export default {
    name: 'ChartsThree',
    data () {
      return {
        title: {
              text: '库位使用情况',
              subText: '库位使用情况'
          },
        option: {
          tooltip: {show:false},
          visualMap: {
              max: 20,
              inRange: {
                  color:color
              }
          },
          xAxis3D: {
              type: 'category',
              name:'行',
              data: []
          },
          yAxis3D: {
              type: 'category',
              name:'层数',
              data: []
          },
          zAxis3D: {
              name:'容量',
              type: 'value'
          },
          grid3D: {
              boxWidth: 200,
              boxDepth: 80,
              viewControl: {
                  // projection: 'orthographic'
                  distance:300,
              },
              light: {
                  main: {
                      intensity: 1.2,
                      shadow: true
                  },
                  ambient: {
                      intensity: 0.3
                  }
              }
          },
          series: [{
              type: 'bar3D',
              data: [],
              shading: 'lambert',
              // silent:true,
              label: {
                  textStyle: {
                      fontSize: 16,
                      borderWidth: 1
                  }
              },

              emphasis: {
                  label: {
                    show:true,
                      textStyle: {
                          fontSize: 20,
                          color: '#900'
                      }
                  },
                  itemStyle: {
                      color: '#900'
                  }
              }
          }]
}
      }
    },
    props: {
      toxAxis3D:{
        type: Array
      },
      toyAxis3D:{
        type:Array
      },
      toseriesData:{
        type:Array
      },
      rangeColor:{
        type:Array
      },
      subtext:{
        type:String,
      }
    },
    watch: {
      toxAxis3D(newVal) {
        if(newVal){
          this.option.xAxis3D.data = newVal
        }
      },
      toyAxis3D(newVal) {
        if(newVal){
          this.option.yAxis3D.data = newVal
        }
      },
      toseriesData(newVal){
        if(newVal){
          this.option.series[0].data = newVal ;
        }
      },
      rangeColor(newVal){
        if(newVal){
          this.option.visualMap.inRange.color = newVal ;
        }
      },
      subtext(newVal){
        if(newVal){
          console.log("sub",newVal)
          this.title.subText = newVal + '库位使用情况'
        }
      }

    },
    components: { CommonWrapper, chart: ECharts }
  }
</script>

<style scoped>
.chart{
  margin:0 auto;
}
.chart{
  margin:0 auto;
}
.title{
  margin-bottom: 10px;
  text-align: center;
}
.mainTitle{
  font-size:16px;
  font-weight: bold;
}
.subTitle{
  font-size:14px;
  color:#999;
}
</style>



